.hero {
  border-radius: 10px;
  background-color: #ebf0fa;
}
.hero__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.hero__main {
  max-width: 538px;
  padding: 102px 72px;
  box-sizing: content-box;
}
.hero__title {
  margin-bottom: 18px;
}
.hero__text {
  margin-bottom: 36px;
}
.hero__img {
  width: 910px;
  height: 456px;
  border-radius: 0 10px 10px 0;
}
.hero__img img {
  border-radius: 0 10px 10px 0;
}